import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.cloudliber.cl_porfolio.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerVariant("-about-section-skills-grid",
                org.cloudliber.cl_porfolio.components.sections.about.components.AboutSectionSkillsGridVariant
        )
        ctx.theme.registerVariant("-nav-bar",
                org.cloudliber.cl_porfolio.components.sections.nav_header.components.NavBarLinkVariant
        )
        ctx.theme.registerVariant("-active-nav-bar",
                org.cloudliber.cl_porfolio.components.sections.nav_header.components.ActiveNavBarLinkVariant
        )
        ctx.theme.registerVariant("-side-menu",
                org.cloudliber.cl_porfolio.components.sections.nav_header.components.SideMenuLinkVariant
        )
        ctx.theme.registerVariant("-active-side-menu",
                org.cloudliber.cl_porfolio.components.sections.nav_header.components.ActiveSideMenuLinkVariant
        )
        ctx.theme.registerVariant("-work-items",
                org.cloudliber.cl_porfolio.components.sections.works.components.WorkItemsSimpleGridVariant
        )
        ctx.theme.registerVariant("-icon",
                org.cloudliber.cl_porfolio.components.widgets.button.IconButtonVariant
        )
        ctx.theme.registerVariant("-primary-icon",
                org.cloudliber.cl_porfolio.components.widgets.button.PrimaryIconButtonVariant
        )
        ctx.theme.registerVariant("-icon-button",
                org.cloudliber.cl_porfolio.components.widgets.button.LinkIconButtonVariant
        )
        ctx.theme.registerVariant("-primary",
                org.cloudliber.cl_porfolio.components.widgets.button.PrimaryButtonVariant
        )
        ctx.theme.registerVariant("-primary-button-with-icon-padding",
                org.cloudliber.cl_porfolio.components.widgets.button.PrimaryButtonWithIconPadding
        )
        ctx.theme.registerVariant("-skill-item",
                org.cloudliber.cl_porfolio.components.widgets.skill.SkillItemImageVariant
        )
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.contact.components.ContactCardStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.footer.FooterStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.nav_header.NavHeaderStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.nav_header.components.AppLogoStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.nav_header.components.NavBarStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.nav_header.components.SideMenuStyle)
        ctx.theme.registerStyle(com.itami.itami_mobile.components.sections.start.components.SkillSectionDividerStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.start.components.StartSectionGridStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.start.components.StartSectionImageStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.works.components.WorkItemStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.works.components.WorkItemImageStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.sections.works.components.WorkItemOverlayStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.widgets.section.SectionContainerStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.components.widgets.skill.SkillItemStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.theme.fonts.TextStyle)
        ctx.theme.registerStyle(org.cloudliber.cl_porfolio.theme.icons.IconStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.components.sections.about.AboutSectionVariant)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.components.sections.contact.ContactSectionVariant)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.components.sections.start.StartSectionVariant)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.TextStylePrimaryColor)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.TextStyleSecondaryColor)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.TextStyleBrandColor)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.DisplayTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.SubDisplayTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.TitleTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.LabelLargeTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.LabelMediumTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.BodyLargeTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.BodyMediumTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.fonts.BodySmallTextStyle)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.icons.IconBrandColorVariant)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.icons.IconOnBrandColorVariant)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.icons.LargeIconSize)
        ctx.theme.registerVariants(org.cloudliber.cl_porfolio.theme.icons.MediumIconSize)
        ctx.theme.registerKeyframes(org.cloudliber.cl_porfolio.components.sections.nav_header.components.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes(org.cloudliber.cl_porfolio.components.sections.start.components.StartSectionGridKeyFrames)
        org.cloudliber.cl_porfolio.initColorMode(ctx)
        org.cloudliber.cl_porfolio.theme.initAppStyles(ctx)
        org.cloudliber.cl_porfolio.theme.initSilkPalette(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "CloudLiber"))
    renderComposable(rootElementId = "root") {
        org.cloudliber.cl_porfolio.AppEntry {
            router.renderActivePage { renderWithDeferred { it() } }
        }
    }
}
